import PNotify from 'pnotify/dist/es/PNotify.js';
import '../node_modules/pnotify/dist/PNotifyBrightTheme.css';
import PNotifyButtons from '../node_modules/pnotify/dist/es/PNotifyButtons.js';
const KEY = '15301640-60ff378e35eeb1b3833f20eb8';
import axios from 'axios';

export default function pixiApi(search, numberPage, callback) {
  const mainURL = `//pixabay.com/api?image_type=photo&orientation=horizontal&q=${search}&page=${numberPage}&per_page=14&key=${KEY}`;
  axios.get(mainURL)
    .then(data => callback(data.data))
    .then(() => {
      window.scrollTo({
        top: document.body.scrollHeight,
        behavior: 'smooth',
      });
    })
    .then(() => {
      PNotify.success({
        title: 'Success!',
        delay: 4000,
        text: 'That thing that you were trying to do worked.',
      });
    })
    .catch(err => {
      PNotify.error({
        title: 'Warning!',
        text: `${err}`,
      });
    });
}