import pixiApi from './apiService.js';
import galaryUl from './galaryUl.hbs';
import debounce from 'lodash.debounce';
import * as basicLightbox from 'basiclightbox';
import './styles.css';
import './basicLightbox.min.css';
import { searchImages } from 'pixabay-api';

const loadButton = document.querySelector('#btn-load-more');
const form = document.querySelector('#search-form');
const input = document.querySelector('#search-form > input');
const gallery = document.querySelector('.gallery');
let page = 1;
let inp = '';

function parseData(data) {
  const dataInp = galaryUl(data);
  gallery.insertAdjacentHTML('beforeend', dataInp);
}
function onInput(e) {
  // console.dir(e.target);
  // console.dir(input.value);

  e.preventDefault();
  const searchQuery = input.value;
  inp = searchQuery;
  if (!searchQuery) {
    return;
  } else {
    pixiApi(searchQuery, page, parseData);
    // console.log('onInput', input.value);
    // searchImages('15301640-60ff378e35eeb1b3833f20eb8', input.value, {page}).then(r => {
    //   console.log('------------------');
    //   console.log(r);
    //   parseData(r);
    //   console.log('------------------');
    // });
  }
}
function onClick(e) {
  e.preventDefault();
  loadButton.setAttribute('disabled', true);
  const searchQuery = inp;
  pixiApi(searchQuery, ++page, parseData);

  // page++;
  // console.log('onClick', input.value);
  // searchImages('15301640-60ff378e35eeb1b3833f20eb8', input.value, {page}).then(r => {
  //   console.log('------------------');
  //   console.log(r);
  //   parseData(r);
  //   console.log('------------------');
  // });

  loadButton.removeAttribute('disabled');
}

function modale(e) {
  if (e.target.localName === 'img') {
    const instance = basicLightbox.create(`
    <img src="${e.target.alt}" >
    `);
    instance.show();
  }
}

loadButton.addEventListener('click', onClick);
form.addEventListener('submit', onInput);
document.body.addEventListener('click', modale);

